.calendar {
    grid-area: cd;
    display: grid;
    grid-template-areas: 
        "... cdg cdg cdg cdg cdg cdg cdg cdg cdg cdg cdg cdg cdg cdg cdg cdg cdg cdg cdg cdg cdg cdg cdg cdg cdg cdg cdg cdg cdg cdg cdg cdg cdg cdg cdg cdg cdg cdg cdg cdg cdg cdg cdg cdg cdg cdg cdg cdg"
        "cdt cdg cdg cdg cdg cdg cdg cdg cdg cdg cdg cdg cdg cdg cdg cdg cdg cdg cdg cdg cdg cdg cdg cdg cdg cdg cdg cdg cdg cdg cdg cdg cdg cdg cdg cdg cdg cdg cdg cdg cdg cdg cdg cdg cdg cdg cdg cdg cdg";
}

.calendar__time {
    grid-area: cdt;
    display: flex;
    flex-direction: column;
    min-width: 30px;
}

.calendar__time-item {
    height: 51px;
    text-align: right;
}

.calendar__time-item-text {
    right: 2px;
    font-size: 10px;
    position: relative;
    top: -10px;
}

.calendar__grid {
    grid-area: cdg;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
}

.calendar__grid-column {
    border-left: 1px solid #1b375e;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.calendar__grid-title {
    margin: 0 auto;
    font-size: 20px;
    font-weight: 500;
}

.calendar__grid-item {
    border-top: 1px solid #1b375e;
    height: 50px;
}